import DateTime from "../DateTime";
import { Title, WrapperList, List, Item, Paragraph, SubTitle, Box, Image } from "./styled";
import { characteristics } from "../../characteristics";
import LinkButton from "../LinkButton";
import Authorities from "../Authorities";
import { StyledSection } from "../StyledSection";

export default () => (
    <>
        <StyledSection>
            <DateTime />
            <Title>Wadze spki:</Title>
            <Authorities />
            <Paragraph>
                Jestemy dynamicznie rozwijajc si spk, ktrej fundamentem jest elektryka, jednak nieustannie poszerzamy zakres naszych usug o instalacje wiatowodowe i fotowoltaiczne. Dziki 8-letniemu dowiadczeniu i nowoczesnemu podejciu realizujemy kompleksowe projekty w nowo powstaych blokach, biurowcach i domach.
            </Paragraph>
        </StyledSection>
        <StyledSection>
            <Title>Co nas wyrnia:</Title>
            <WrapperList>
                <List>
                    {characteristics.map(({ name, description, image }) => (
                        <Item key={name}>
                            <Box>
                                <SubTitle>{name}</SubTitle>
                                <Paragraph $characteristicParagraph>
                                    {description}
                                </Paragraph>
                            </Box>
                            <Image src={image} alt={name}></Image>
                        </Item>
                    ))}
                </List>
            </WrapperList>
            <Paragraph $margins>
                Tworzymy instalacje dostosowane do wspczesnych potrzeb, zapewniajc naszym klientom komfort, bezpieczestwo i dostp do nowoczesnych technologii.
            </Paragraph>
            <LinkButton />
        </StyledSection>
    </>
);