import Hero from '../Hero';
import About from '../About';
import ServiceList from '../ServiceList';
import Slider from '../Slider';
import { slides } from '../../slides';
import { Title, BlackContent, Description } from "./styled";
import { StyledSection } from "../StyledSection";

export default () => (
    <>
        <Hero />
        <StyledSection>
            <About />
        </StyledSection>
        <StyledSection $displayMiddle>
            <Title>Obszary dziaalnoci</Title>
            <br />
            <Description>
                Wybierz usug, z ktrej chcesz skorzysta, a my zajmiemy si reszt! Profesjonalna realizacja, nowoczesne rozwizania i najwysza jako usug.
            </Description>
            <ServiceList />
        </StyledSection>
        <StyledSection $displayMiddle $offMargin>
            <Title>
                Co Mwi
                <br />
                <BlackContent>O Nas Klienci</BlackContent>
            </Title>
            <Description>
                Zaufao nam setki klientw, docz do nich i bd zadowolony z wykonanej przez nas usugi
            </Description>
            <Slider slides={slides} />
        </StyledSection>
    </>
);